.loader {
  position: fixed;
  top: 0;
  height: 100vh;
  width: 100vw;
  z-index: 99;
  background: rgba(0, 0, 0, 0.726); }
  .loader__text {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -100px;
    margin-left: -150px;
    font-size: 24px;
    color: white; }
  .loader__blocks {
    position: absolute;
    width: 100px;
    justify-content: space-around;
